@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;400;500&display=swap);
:root {
    --primary: #434343;
    --accent: #2a978b;
}

html, body {
    background: #434343;
    background: var(--primary);
    height: 100vh;
    color: #fff;
    font-family: 'Poppins', sans-serif;
}


.app{display:flex;flex-direction:column;justify-content:center;padding:0 0 20px 0}.logo-container{display:flex;width:100%;justify-content:center}.search-bar--container{width:50%;min-width:320px;max-width:700px;margin:0 auto;padding:0 0 20px 0}

.form--container{display:flex;width:70%;margin:0 auto;padding:20px;grid-gap:10px;gap:10px}

.cards{display:grid;grid-template-columns:repeat(auto-fit, minmax(250px, 1fr));grid-gap:30px;gap:30px;width:90%;margin:0 auto}.card{display:grid;flex-direction:column;grid-template-rows:100px 1fr 75px;position:relative;align-items:center;width:250px;box-shadow:rgba(16,16,16,0.36) 0 2px 8px 0;overflow:hidden;transition:0.3s}.card:hover{cursor:pointer}.card:active{box-shadow:#2a978c -1px 1px 16px}.card:hover .card__info-icon{visibility:visible;bottom:40px;right:40px;-webkit-transform:scale(3);transform:scale(3)}.card:hover .card__image{-webkit-transform:scale(1.7);transform:scale(1.7)}.card__info-icon{position:absolute;top:35%;left:50%;width:80px;height:80px;color:var(--accent);-webkit-transform:scale(0);transform:scale(0);visibility:hidden;opacity:0.6;transition:1s}.card__heading{font-size:22px;font-weight:500;line-height:1.2;padding:0 0 10px 10px}.card__image-container{display:flex;height:350px;padding:15px 0;border-top:2px solid floralwhite}.card__image{height:100%;width:100%;transition:-webkit-transform 0.5s;transition:transform 0.5s;transition:transform 0.5s, -webkit-transform 0.5s;object-fit:cover}.card__image--alternative{height:100%;width:100%}.card__summary{display:flex;padding:5px 5px 10px 5px}

.search-bar{display:flex;width:100%;padding:20px;font-size:20px;border:0;border-radius:5px;outline:none}.search-bar:focus{box-shadow:#2a978c -1px 1px 16px}

.button{background-color:var(--accent);padding:10px 40px;color:floralwhite;font-size:18px;font-family:inherit;border:1px solid transparent;border-radius:5px;opacity:0.87;transition:0.2s}.button:hover{cursor:pointer;opacity:1}.button:active{box-shadow:rgba(50,50,93,0.25) 0 30px 60px -12px inset,rgba(0,0,0,0.3) 0px 18px 36px -18px inset}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;vertical-align:baseline}a{text-decoration:none;color:inherit}*{box-sizing:border-box;margin:0;padding:0}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:''}table{border-collapse:collapse;border-spacing:0}

.error-message--container{width:90%;margin:0 auto;padding:20px}.error-message{font-size:32px;font-weight:500}

.show--container{display:grid;grid-template-columns:1fr 2fr;line-height:1.4}.link{padding:0 20px;text-decoration:none;font-size:20px;color:white;opacity:0.7;transition:0.1s}.link:hover{color:var(--accent)}.link:active{opacity:1}.show__heading{display:flex;grid-column:1 / 3;justify-content:center;align-items:center;padding:20px 0;margin:10px 0;font-size:28px;border-top:4px solid #9b9b98;border-bottom:4px solid #9b9b98}.show__summary-section{display:grid;grid-gap:10px;gap:10px;padding:20px 0;margin:5px;border-bottom:4px solid #9b9b98}.show__episodes-section{display:flex;flex-direction:column;flex-wrap:wrap;grid-gap:10px;gap:10px;padding:20px 0;margin:5px;border-bottom:4px solid #9b9b98}.show__image-container{height:500px;width:100%;justify-content:center;align-items:center;overflow:hidden;padding:10px}.show__image--alternative{height:100%;width:100%}.show__image{height:100%;width:100%;object-fit:scale-down}

table{table-layout:fixed;width:100%;border-collapse:collapse;border:1px solid var(--accent)}thead th:nth-child(1){width:20%}thead th:nth-child(2){width:20%}thead th:nth-child(3){width:60%}th,td{padding:10px}tbody td{text-align:center}thead,tfoot{color:white;text-shadow:1px 1px 1px black}thead th,tfoot th,tfoot td{background:linear-gradient(to bottom, rgba(0,0,0,0.1), rgba(0,0,0,0.5));border:1px solid var(--accent)}tbody tr:nth-child(odd){background-color:#444444}tbody tr:nth-child(even){background-color:#777676}

